/************************************************navbar style*******************************************************************/

.css-11b3ww9-MuiPaper-root-MuiAppBar-root {
  background-color: white !important;
  box-shadow: 0px 2px 2px lightgray !important;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root img {
  width: 250px;
  margin-left: 20px;
}

.css-11b3ww9-MuiPaper-root-MuiAppBar-root a,
header a {
  color: #757575 !important;
  padding: 10px !important;
  display: inline-block !important;
  text-decoration: none !important;
  transition: 1s !important;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:hover,
header a:hover {
  color: #ef3575 !important;
  cursor: pointer !important;
  transition: 1s;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:active,
header a:active {
  color: #ef3575 !important;
}

/* ***********************************************************footer style **************************************************************/
.footer {
  padding: 70px 40px;
}
.footer a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}
.icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}
.icons a {
  color: #293e5a;
  display: flex;
  align-items: center;
  align-content: center;
  text-decoration: none;
  padding: 3px 6px;
  padding-top: 6px;
  display: inline-block;
  border-radius: 50%;
  background-color: white;
}
