.left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  /* height: 300px; */
  animation: abs 1 1s;
  width: 80px;
}
.right {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(0%) scale(-1);
  /* height: 300px; */
  width: 80px;
  animation: abs 1 1s;
}

.css-xnhscb-MuiCardMedia-root {
  min-width: 350px;
}
@keyframes abs {
  from {
    width: 0px;
    opacity: 0;
  }
  to {
    opacity: 1;
    width: 80px;
  }
}
